.assistant-msg {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.msg-avtar {
    width: 40px;
    height: 40px;
}

.msg-avtar img {
    width: 100%;
    height: 100%;
    border-radius: var(--border-radius-1);
}

.msg-list {
    margin-left: 10px;
    height: 40px;
    max-width: calc(80% - 40px);
}

.msg-item {
    max-width: calc(100% - 20px);
    height: 40px;
    padding: 0 10px;
    background:var(--main-bg-color-2);
    border-radius: var(--border-radius-1);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-small);
    overflow: auto;
}