.talk {
    height: 100%;
    width: 100%;
    font-size: var(--font-size-mid);
}

.talk-history {
    height: calc(100% - 60px - 20px);
    width: calc(100% - 20px);
    margin: 10px;
    display: flex;
    justify-content: end;
    flex-direction: column;
    gap: 10px;
    overflow: auto;
}

.talk-history-left-row {
    width: calc(100% - 20px);
    display: flex;
    justify-content: start;
    padding: 0 10px;
}

.talk-history-left {
    background: var(--main-bg-color-2);
    max-width: calc(90% - 20px);
    min-width: 50px;
    min-height: 30px;
    padding: 10px;
    border-radius:  var(--border-radius-1) var(--border-radius-1)  var(--border-radius-1) 0;
}

.talk-history-right-row {
    width: calc(100% - 20px);
    display: flex;
    justify-content: end;
    padding: 0 10px;
}

.talk-history-right {
    background: var(--priary-color);
    max-width: calc(90% - 20px);
    min-width: 50px;
    min-height: 30px;
    padding: 10px;
    border-radius:  var(--border-radius-1) var(--border-radius-1) 0  var(--border-radius-1);
    word-break: break-all;
    color: white;
}

.talk-sender {
    height: 60px;
    width: calc(100% - 40px);
    padding: 0 20px;
    display: flex;
}

.talk-sender-input {
    width: calc(100% - 20px - 60px);
    height: 100%;
    padding: 0 10px;
    background: var(--main-bg-color-2);
    color: var(--main-text-color-1);
    font-size: var(--font-size-mid);
    border-top-left-radius: var(--border-radius-1);
    border-bottom-left-radius: var(--border-radius-1);
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border: none;
    outline: none;
    box-shadow: 0 6px 10px 0 var(--main-text-color-3);
}

.talk-send-btn {
    width: 60px;
    height: 100%;
    background: var(--main-bg-color-2);
    border-top-right-radius: var(--border-radius-1);
    border-bottom-right-radius: var(--border-radius-1);
    color: var(--priary-color);
    font-weight: bold;
    font-size: var(--font-size-small);
    cursor: pointer;
    box-shadow: 0 6px 10px 0 var(--main-text-color-3);
}