.page-head {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    height: 50px;
    z-index: var(--max-zindex);
}

.page-main {
    position: absolute;
    left: 0;
    right: 0;
    top: 50px;
    bottom: 80px;
}

.page-foot {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 80px;
}

.menu-bar {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-bar-item {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: var(--font-size-mid);
    color: var(--main-text-color-2);
    cursor: pointer;
}
.menu-bar-item.active {
    font-weight: bold;
    color: var(--main-text-color-1);
    color: var(--priary-color);
}
.flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
}