:root {
    --main-bg-color-1: #f5f5f7;
    --main-bg-color-2: white;
    --main-text-color-1: balck;
    --main-text-color-2: rgb(0, 0, 0, 0.5);
    --main-text-color-3: rgb(0, 0, 0, 0.1);
    /* common */
    --priary-color: #0077ED;
    --min-zindex: 0;
    --middle-zindex: 50;
    --max-zindex: 100;
    --font-size-small: 16px;
    --font-size-mid: 20px;
    --font-size-big: 30px;
    --border-radius-1: 24px;
}

:root[theme='dark'] {
    --main-bg-color-1: rgb(29, 29, 31);
    --main-bg-color-2: black;
    --main-text-color-1: white;
    --main-text-color-2: rgb(255, 255, 255, 0.5);
    --main-text-color-3: rgb(255, 255, 255, 0.1);
}

body {
    background: var(--main-bg-color-1);
    color: var(--main-text-color-1);
}